import React from 'react';


class Gm extends React.Component {
	//shouldcomponent update!!
	constructor(props) {
    super(props);}
  
   
    
	componentWillMount() {
      
    }
    componentDidMount(){
    	    	
    		var mapOptions = {
          center: { lat: 52.397, lng: 5.644},
          zoom: 8
        };
        var map = new this.props.google.maps.Map(this.refs.map,
            mapOptions);
            this.props.setMap(map)
          //  this.props.getUserInfo()
    //tp.mapInitialized();
    	
    	//render locations of gm on change
    	
        	
        	
    }
	
	render(){
	
		//console.log('rendering map')
		//initmap
		return(<div>
			<div ref='map' id='map'>
			loading google maps api
			</div>
			
			<button onClick={this.props.newMarker.bind(null,-34.397,150.644,99)}/>
		
			</div>
		)
		
	
	}
	}

export default Gm;


