import React from 'react';

import {Link} from 'react-router';

class Form extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: ''
        };

        this.handleChange = this.handleChange.bind(this);

    }
    handleChange(event) {
        this.props.changeSearch(event.target.value);
    }

    render() {

        return (
            <div className='form'>

                <input type="text" onChange={this.handleChange}/>
                <button onClick={this.props.showNotificationWithTimeout.bind(null, this.props.global.searchterm)} className="likes">&hearts;
                </button>
            </div>
        )

    }
}

export default Form;


