function notification(state = [], action) {
  switch(action.type) {
    case 'SHOW_NOTIFICATION' :
      console.log('show notification');
      
     // console.log(state)
      return action.text;
     case 'HIDE_NOTIFICATION' :
      console.log("hide notification");
     // console.log(state)
      return "no message";
    
    default:
      return state;
  }
}

export default notification;



