import {createStore,compose,applyMiddleware} from 'redux';
import {syncHistoryWithStore} from 'react-router-redux';
import {browserHistory} from 'react-router';
import thunk from 'redux-thunk';

//import root reducer

import rootReducer from './reducers/index';
function getCookie(cname) {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(';');
    for(var i = 0; i <ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
        }
    }
    return "";
}
import comments from './data/comments';
import persons from './data/persons';
import posts from './data/posts';
const counter =1;
const global = {products:[{}],refreshing:false,searchstring:'amsterdam',token:getCookie('ctoken'),error:'none',user:getCookie('cname'),loggedin:getCookie('cloggedin')};
const scriptloaded = 0;
const user =0;
const gm={scriptloaded:0,initmap:0,places:[]};
const notification="no message";
const defaultState = {

	posts,
	comments,
	counter ,
	notification,
	persons,
	global,
	scriptloaded,
	gm,
	user
}
const store = createStore(rootReducer, defaultState, applyMiddleware(thunk));
export const history = syncHistoryWithStore(browserHistory, store )
export default store;


