import React from 'react';
import { Link } from 'react-router';
import CSSTransitionGroup from 'react-addons-css-transition-group';

const Person = React.createClass({
	componentDidMount(){},
  render() {
  	
    const { person, i } = this.props;
    return (
    	<figure className="grid-figure" onMouseOver={this.props.raiseMarker.bind(null,person.id)}>
    <div className="grid-photo-wrap">{person.personname} <br></br> {person.subtitle}</div>
   <img src={person.imageUrl}/>
   <div className="control-buttons">
   <button className="likes" onClick={this.props.raiseMarker.bind(null,person.id)}>op kaart</button>
   </div>
  
   
   
    </figure>
    )
  }
});

export default Person;


