import React from 'react';

import {Link} from 'react-router';

const User = React.createClass({


    login(that) {

        this.props.login(that.refs.name.value, that.refs.password.value);
        //console.log(name.value,pw.value);
    },
		refreshbutton(that) {
//this.props.refreshing==true
				if(this.props.global.refreshing==false)return <button onClick={this.props.refresh.bind(this)} className="likes">refresh</button>
else return <button type='button' disabled onClick={this.props.refresh.bind(this)} className="likes">refresh</button>
			//console.log(name.value,pw.value);
		},
    componentDidMount() {},
    render() {

        return (
            <div className='usert'>

                <input ref="name" type="text"/>
                <br></br>
                <input ref="password" type="text"/>
                <br></br>
                //{this.props.global.token}
                <br></br>
                {this.props.global.error}<br></br>
                <button onClick={this.login.bind(this, this)} className="likes">Log in
                </button>


            </div>
        )

    }
})

export default User;


