import React from 'react';
import { Link } from 'react-router';
import ProductsGrid from "../components/ProductsGrid";
import UserData from "../components/UserData";
const Office = React.createClass({
	newproduct(that) {
			this.props.newProduct(this.props.global.token, that.refs.pname.value)
			// this.props.login(that.refs.name.value,that.refs.password.value);
			//console.log(name.value,pw.value);
	},
	login(that) {

			this.props.login(that.refs.name.value, that.refs.password.value);
			//console.log(name.value,pw.value);
	},
	refreshbutton(that) {
//this.props.refreshing==true
			if(this.props.global.refreshing==false)return <button onClick={this.props.refresh.bind(this)} className="likes">refresh</button>
else return <button type='button' disabled onClick={this.props.refresh.bind(this)} className="likes">refresh</button>
		//console.log(name.value,pw.value);
	},
	componentDidMount(){console.log('office')},

	render(){

		return(
			<div>
<div>
				<button onClick={this.props.getProducts.bind(this, this.props.global.token, 'products')} className="likes">load stuff
				</button>
				<input ref="pname" type="text"/>
				<br></br>
				<button onClick={this.newproduct.bind(null, this)} className="likes">add product
				</button>
			</div>
			<div>
				{this.refreshbutton(null)}
				<ProductsGrid {...this.props} />
</div>

			</div>
		)

	}
	})

export default Office;


