function global(state = [], action) {
    switch (action.type) {

        case 'CHANGE_SEARCH':
            console.log(action.text)
            // a= action.text
            const state1 = {
                ...state
            };
            state1.searchterm = action.text;
            return state1;
        case 'GET_TOKEN':
            console.log('token', action.json)
            // a= action.text
            const state2 = {
                ...state
            };
            state2.token = action.json;
            document.cookie = 'ctoken=' + action.json;
            state2.error = 'succes';
            state2.loggedin = true;
            document.cookie = 'cloggedin=true'
            return state2;

        case 'LOGIN_ERROR':
            console.log('token', action.json)
            // a= action.text
            const state3 = {
                ...state
            };
            state3.error = action.json;
            return state3;
        case 'HANDLE_NEW_OBJECT':
            console.log('resource', action.json)
            // a= action.text
            const state4 = {
                ...state
            };
            state4.user = action.json.name;
            document.cookie = 'cname=' + action.json.name;
            return state4;
            case 'HANDLE_GET_PRODUCTS':
                console.log('resource', action.json)
                // a= action.text
                const state7 = {
                    ...state
                };
                state7.products = action.json;
            //    document.cookie = 'cname=' + action.json.name;
                return state7;
                case 'HANDLE_DELETE_PRODUCT':
                    console.log('resource', action.json)
                    // a= action.text
                    const state8 = {
                        ...state
                    };
                    state8.products = action.json;
                //    document.cookie = 'cname=' + action.json.name;
                    return state8;
        case 'START_REFRESH':

            const state5 = {
                ...state
            };
            state5.refreshing = true;

            return state5;
        case 'FINISH_REFRESH':

            const state6 = {
                ...state
            };
            state6.refreshing = false;

            return state6;

        default:
            return state;
    }
}

export default global;



