function gm(state = [], action) {
  switch(action.type) {
     
     case 'SET_MAP' :
     const state5={...state}
 //google.maps.event.trigger(MapInstance,'resize')
 state5.map=action.map 
   return state5;
      case 'RENDER_LOCATIONS' :
     console.log(action)
  const state6={...state}
     for (let s of action.persons) {
     	console.log(s.lat,s.lng)
     	
  var marker = new google.maps.Marker({
          position: { lat: parseFloat(s.lat), lng: parseFloat(s.lng)},
          map: state.map,
          title: s.personname
        });
 
  state6.places.push({lat: s.lat, lng: s.lng,pid:s.id,marker:marker}) 
     	
     	
}
     return state6;
     case 'GM_NEW_MARKER' :
     console.log('newmarker')
  const state4={...state}
  var marker = new google.maps.Marker({
          position: { lat: action.lat, lng: action.lng},
          map: state.map,
          title: 'Hello World!'
        });
  state4.places.push({lat: action.lat, lng: action.lng,pid:action.id,marker:marker}) 
   return state4;
  
  
    case 'MAP_INITIALIZED' :
      
       const state3={...state}
       //state2.scriptloaded=1;
     state3.initmap=1;
      return state3;
    
    case 'GM_LOADED' :
      
       const state2={...state}
       //state2.scriptloaded=1;
     state2.scriptloaded=1;
     
     
     
      return state2;
     case 'RAISE_MARKER' :
      console.log(action.id)
      
for (let s of state.places) {
s.marker.setAnimation(null)
	if (s.pid == action.id) {
		state.map.setCenter({
			lat : parseFloat(s.lat),
			lng : parseFloat(s.lng)
		})
		s.marker.setAnimation(google.maps.Animation.BOUNCE)
	var m = s.marker;
	
		
			setTimeout(function(m) {
				m.setAnimation(null)
			}, 2000,m)
		
	}
}

     //  const state7={...state}
       //state2.scriptloaded=1;
    // state2.scriptloaded=1;
     
     
     
      return state;
    
    default:
      return state;
  }
}

export default gm;



