import React from 'react';
import Person from './Person';
import { Link } from 'react-router';
//send through the props to photo and the index (not available through key)
const PersonsGrid = React.createClass({
	
	render(){
		
		return(
			<div className='photo-grid'>
			
			
			{this.props.persons.map((person,i)=> <Person {...this.props} key={i} i={i} person={person}/>)}
				 
            

			</div>
		)
		
	}
	})

export default PersonsGrid;


