export
function handleResource(json,type) {

return function(dispatch) {
switch (type){
    case "newproduct": dispatch(handleNewObject(json))
      case "getProducts": dispatch(handleGetProducts(json))
      case "deleteProduct": dispatch(handleDeleteProduct(json))
  }
}
}
