let nextNotificationId = 0
export
function showNotificationWithTimeout(text) {
    return function(dispatch) {
        // const id = nextNotificationId++
        dispatch(showNotification(3, 'loading'));
        fetch('http://www.sublation.nl/search?waar=' + text).then(function(response) {
            if (response.status !== 200) {
                dispatch(showNotification(3, 'error'));
                console.log('Looks like there was a problem. Status Code: ' + response.status);
                return;
            }

            response.json().then(function(data) {
                dispatch(receivePosts(data));
                dispatch(renderMarkers(data));
                dispatch(hideNotification(0));
                console.log(data)
            });
        }).catch(function(err) {
            console.log('Fetch Error :-S', err);
        });

    }
}
export
function login(name, pw) {
    return function(dispatch) {
        // const id = nextNotificationId++
        dispatch(showNotification(3, 'loading'));
        fetch('http://localhost/api4/web/app_dev.php/users/login?password=' + pw + '&name=' + name, {credentials: 'include'}).then(function(response) {
            if (response.status !== 200) {
                dispatch(showNotification(3, 'error'));
                console.log('Looks like there was a problem. Status Code: ' + response.status);
                return;
            }

            //todoCLEARLOGIN
            response.json().then(function(data) {
                if (!data.error) {
                    dispatch(getToken(data.token));
                    dispatch(getResource(data.token, 'user'))
                } else
                    dispatch(loginError(data.error_description))
                console.log(data)
            });
        }).catch(function(err) {
            console.log('Fetch Error :-S', err);
        });

    }
}


