export
function getResource(token, url,type) {

    return function(dispatch) {

        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");
        myHeaders.append("Authorization", "Bearer " + token);
        fetch(url, {headers: myHeaders}).then(function(response) {
            if ((response.status !== 200) && (response.status !== 401)) {

                console.log('Looks like there was a problem. Status Code: ' + response.status);
                return;
            }

            response.json().then(function(data) {
                if (!data.error) {
                    dispatch(handleResource(data,type));

                } else {

                    if (data.error_description == 'The access token provided is invalid.') {
                        console.log('token invalid~!!!')
                        //LOGGEDOUT!
                    }
                    if (data.error_description == 'The access token provided has expired.') {
                        console.log('token expired~!!!')
                        console.log('attempting token refresh')
                        dispatch(refresh(getResource(token, url)));
                    }
                }
                // console.log(data)
            });
        }).catch(function(err) {
            console.log('Fetch Error :-S', err);
        });

    }
}

export
function refresh() {
    return function(dispatch) {dispatch(startRefresh())
//check for concurent refresh! then delay
        fetch('http://localhost/api4/web/app_dev.php/users/refresh', {credentials: 'include'}).then(function(response) {
            if (response.status !== 200) {
                console.log('Looks like there was a problem. Status Code: ' + response.status);
                return;
            }

            response.json().then(function(data) {
                if (!data.error) {

                    dispatch(getToken(data));
                  //  dispatch(getResource(data, 'user'));
dispatch(finishRefresh())
                    return
                } else
                    dispatch(loginError(data.error_description))

            });
        }).catch(function(err) {
            console.log('Fetch Error :-S', err);
        });

    }
}

export
