function persons(state = [], action) {
  switch(action.type) {
    case 'RECEIVE_POSTS' :
      console.log(action.json);
      
     // console.log(state)
      return action.json;
    
      
      console.log(state)
     // console.log(state)
      return state;
     
    default:
      return state;
  }
}

export default persons;



