// let's go!
import React from 'react';
import  {render} from 'react-dom';
//import css
import css from './styles/style.styl'

// import components
import App from "./components/App" //redux metalayer for main
import Single from "./components/Single"
import Office from "./components/Office"
import Login from "./components/Login"
import PersonsGrid from "./components/PersonsGrid"

//import react router dependencies
import {Router , Route , IndexRoute, browserHistory } from 'react-router' ;
import {Provider} from 'react-redux';// redux to react provider
import store,{history} from './store';
const router = (
	<Provider store={store}>

		<Router history={history}>
		  <Route path="/api4/web/app_dev.php" component={App}>

		  <IndexRoute component={PersonsGrid}></IndexRoute>
		  <Route path="/api4/web/app_dev.php/view/:postId" component={Single}></Route>
		  <Route path="/api4/web/app_dev.php/office" component={Office}></Route>
		  <Route path="/api4/web/app_dev.php/login" component={Login}></Route>
		  </Route>
		</Router>
	</Provider>

)

render(router,document.getElementById('root'))



