import React from 'react';
import Notification from './Notification';
import { Link } from 'react-router';
import Form from "../components/Form";
import Map from "../components/Map";
import CSSTransitionGroup from 'react-addons-css-transition-group';
import Gm from "../components/Gm";
import TopBar from "../components/TopBar";
const Main = React.createClass({

	googlemap(scriptloaded){


		if(scriptloaded!=0){
		return(<Gm google={window.google} {...this.props}/>)}

		},
	componentDidMount(){
	var self=this;
	    const script = document.createElement("script");
		console.log(this.props)
        script.src = "https://maps.googleapis.com/maps/api/js?key=AIzaSyBHmwK2ycQXmt1Dd-HFYLeKmUByXwtpE0A";
        script.async = true;
        script.onload=function(){

        	initialize();
        	//console.log(that)

        	}

        document.body.appendChild(script);

         function initialize() {
         	self.props.googleMapsLoaded();
         	//console.log(self.props)
       // console.log(document.cookie);
      }
	},
	render(){

		return(
			<div>
			<TopBar  {...this.props}/><CSSTransitionGroup transitionName="like" transitionEnterTimeout={500} transitionLeaveTimeout={500}>
				<span key={this.props.counter} className="likes-heart">{this.props.counter}</span>
			</CSSTransitionGroup>
			<h1>

			<Link  to="/api4/web/app_dev.php/">Sublation{this.props.counter}</Link>
			</h1>
			<button onClick={this.props.incrementcounter}>increase</button>
			<Link  className='navbutton' to="/api4/web/app_dev.php/">Home</Link>
			<Link  className='navbutton' to="/api4/web/app_dev.php/office">products</Link>
			<Link  className='navbutton' to="/api4/web/app_dev.php/view/e">view</Link>
			<Link  className='navbutton' to="/api4/web/app_dev.php/login">log in</Link>
			{this.googlemap(this.props.gm.scriptloaded)}


			<Form  {...this.props}/>
			{this.props.counter}

			<h2><Notification {...this.props}/></h2>
			{React.cloneElement(this.props.children,this.props)}
			</div>
		)

	}
	})

export default Main;


