import React from 'react';
import { Link } from 'react-router';
import CSSTransitionGroup from 'react-addons-css-transition-group';

const Product = React.createClass({
	componentDidMount(){},
  render() {

    const { product, i } = this.props;
    return (
			<Link to={`/api4/web/app_dev.php/view/${product.id}`}>
    	<figure className="grid-figure" >
    <div className="grid-photo-wrap">{product.name} <br></br> {product.id}</div>
		<button onClick={this.props.deleteProduct.bind(this,this.props.global.token,product.id)}>X</button>
			



    </figure></Link>
    )
  }
});

export default Product;


